import { Toast } from 'src/index';

export default {
  methods: {
    openToast() {
      Toast('');
    },

    openToastWithIcon() {
      Toast({
        message: '',
        iconClass: 'mintui mintui-success'
      });
    },

    openBottomToast() {
      Toast({
        message: '',
        position: 'bottom'
      });
    }
  }
};



