import { MessageBox } from 'src/index';

export default {
  methods: {
    openAlert() {
      MessageBox.alert('!', '');
    },

    openConfirm() {
      MessageBox.confirm('?', '');
    },

    openPrompt() {
      MessageBox.prompt(' ', '').then(({ value }) => {
        if (value) {
          MessageBox.alert(` ${ value }`, '');
        }
      });
    }
  }
};



