import XTextarea from 'bh-mint-ui2/packages/textarea/index.js';
export default {
  name: 'page-radio',

  data() {
    return {
      value0: '',
      value1: '',
      value2: 'A',
      value3: '',
      textareaContent:''
    };
  },

  created() {
    this.options0 = ['A', 'B', 'C'];
    this.options1 = ['A', 'B', 'C'];
    this.options3 = ['A', 'B', 'C'];
    this.options2 = [
      {
        label: '',
        value: 'F',
        disabled: true
      },
      {
        label: 'A',
        value: 'A'
      },
      {
        label: 'B',
        value: 'B'
      }
    ];
  },
  components: {
    XTextarea
  }
};



