import XCell from 'bh-mint-ui2/packages/cell/index.js';
import Clickoutside from 'bh-mint-ui2/src/utils/clickoutside';
if (process.env.NODE_ENV === 'component') {
  require('bh-mint-ui2/packages/cell/style.css');
}

/**
 * mt-field
 * @desc  cell
 * @module components/field
 *
 * @param {string} [type=text] - field  text, textarea 
 * @param {string} [label] - 
 * @param {string} [rows] - textarea  rows
 * @param {string} [placeholder] - placeholder
 * @param {string} [disabled] - disabled
 * @param {string} [readonly] - readonly
 * @param {string} [state] -  error, warning, success
 *
 * @example
 * <mt-field v-model="value" label=""></mt-field>
 * <mt-field v-model="value" label="" placeholder=""></mt-field>
 * <mt-field v-model="value" label="" placeholder="" type="textarea" rows="4"></mt-field>
 * <mt-field v-model="value" label="" placeholder="" state="success"></mt-field>
 */

/**
 * @noteType component
 * @name Field
 * @tagName mt-field
 * @desc 
 * @wrapClassName mint-field
 * @html
 * <mt-field label=""></mt-field>
 */
export default {
  name: 'mt-field',

  data() {
    return {
      active: false,
      currentValue: this.value
    };
  },

  directives: {
    Clickoutside
  },

  props: {
      /**
       * @noteType prop
       * @field type
       * @desc 
       * @type select
       * @option
       [
       {
         "text": "",
         "value": ""
       },
       {
         "text": "",
         "value": "password"
       },
       {
         "text": "",
         "value": "date"
       },
       {
         "text": "",
         "value": "number"
       },
       {
         "text": "",
         "value": "textarea"
       },
       {
         "text": "",
         "value": "email"
       },
       {
         "text": "",
         "value": "url"
       },
       {
         "text": "",
         "value": "tel"
       }
       ]
       */
    type: {
      type: String,
      default: 'text'
    },
    rows: String,
      /**
       * @noteType prop
       * @field label
       * @desc 
       * @type input
       * @value 
       */
    label: String,
      /**
       * @noteType prop
       * @field placeholder
       * @desc placeholder
       * @type input
       * @value
       */
    placeholder: String,
      /**
       * @noteType prop
       * @field readonly
       * @desc 
       * @valueType boolean
       * @type radio
       * @value false
       * @option
       [
       {
           "text": "",
           "value": false
       },
       {
           "text": "",
           "value": true
       }
       ]
       */
    readonly: Boolean,
      /**
       * @noteType prop
       * @field disabled
       * @desc 
       * @valueType boolean
       * @type radio
       * @value false
       * @option
       [{
           "text": "",
           "value": false
       },{
           "text": "",
           "value": true
       }]
       */
    disabled: Boolean,
    disableClear: Boolean,
      /**
       * @noteType prop
       * @field state
       * @desc 
       * @type select
       * @option
       [
       {
         "text": "",
         "value": ""
       },{
         "text": "",
         "value": "success"
       },{
         "text": "",
         "value": "error"
       },{
         "text": "",
         "value": "warning"
       }]
       */
    state: {
      type: String,
      default: 'default'
    },
    /**
     * @noteType prop
     * @field value
     * @desc 
     * @type textarea
      */
    value: {},
    attr: Object
  },

  components: { XCell },

  methods: {
    doCloseActive() {
      this.active = false;
    },

    handleInput(evt) {
      this.currentValue = evt.target.value;
    },

    handleClear() {
      if (this.disabled || this.readonly) return;
      this.currentValue = '';
    }
  },

  watch: {
    value(val) {
      this.currentValue = val;
    },

    currentValue(val) {
      this.$emit('input', val);
    },

    attr: {
      immediate: true,
      handler(attrs) {
        this.$nextTick(() => {
          const target = [this.$refs.input, this.$refs.textarea];
          target.forEach(el => {
            if (!el || !attrs) return;
            Object.keys(attrs).map(name => el.setAttribute(name, attrs[name]));
          });
        });
      }
    }
  }
};
/**
 * @noteType external
 * @content
 {
   "xtype": [ "text", "textarea" ],
   "bindField": "v-model"
 }
 */



